import { _decorator, Component, Node } from 'cc';
import { ReelView } from './ReelView';
const { ccclass, property } = _decorator;

// ÊØèÂÄã symbol Â∞çÊáâ‰∏≠ÁçéÂÄçÁéá
const symbolPayTable: Record<string, number> = {
    M1: 2,
    M2: 3,
    M3: 5,
    M4: 8,
    M5: 10,
    WILD: 15,
    SC: 0
};

@ccclass('SlotController')
export class SlotController extends Component {
    @property(Node) reel1: Node = null;
    @property(Node) reel2: Node = null;
    @property(Node) reel3: Node = null;

    private isSpinning: boolean = false;

    onSpinButtonClick() {
        if (this.isSpinning) return;
        this.isSpinning = true;
        console.log('üé∞ ÈñãÂßãËΩâËª∏');

        const r1 = this.reel1.getComponent(ReelView);
        const r2 = this.reel2.getComponent(ReelView);
        const r3 = this.reel3.getComponent(ReelView);

        // Ë®≠ÂÆöÊØèËª∏ÈÄüÂ∫¶ËàáËΩâÂãïÁßíÊï∏
        r1.roundsPerSecond = 5;
        r1.spinDuration = 1.5;

        r2.roundsPerSecond = 4;
        r2.spinDuration = 2.0;

        r3.roundsPerSecond = 3;
        r3.spinDuration = 2.5;

        // ÈñãÂßãÊóãËΩâ
        r1.spin();
        r2.spin();
        r3.spin();

        // Á≠âÂæÖÁµêÊùüÔºà‰øùÂÆà‰º∞Á¥Ñ 2.8 ÁßíÔºâ
        setTimeout(() => {
            const [r1top, r1mid, r1bot] = r1.getSymbols();
            const [r2top, r2mid, r2bot] = r2.getSymbols();
            const [r3top, r3mid, r3bot] = r3.getSymbols();

            const lineNames = ['‰∏äÁ∑ö', '‰∏≠Á∑ö', '‰∏ãÁ∑ö', '‚ÜòÊñúÁ∑ö', '‚ÜóÊñúÁ∑ö'];
            const allLines = [
                [r1top, r2top, r3top],     // ‰∏ä
                [r1mid, r2mid, r3mid],     // ‰∏≠
                [r1bot, r2bot, r3bot],     // ‰∏ã
                [r1top, r2mid, r3bot],     // ‚Üò
                [r1bot, r2mid, r3top]      // ‚Üó
            ];

            const rowMap = [0, 1, 2, 0, 2]; // ÊØèÊ¢ùÁ∑öÂ∞çÊáâÁöÑ Row Á¥¢ÂºïÔºà‚Üò‚Üó ÈúÄÁâπÂà•ËôïÁêÜÔºâ
            const winLines: string[] = [];
            let totalWin = 0;

            console.log('üéØ ‰∫îÁ∑öÁµêÊûúÔºö');
            console.log(`‰∏äÁ∑öÔºö${allLines[0].join(' | ')}`);
            console.log(`‰∏≠Á∑öÔºö${allLines[1].join(' | ')}`);
            console.log(`‰∏ãÁ∑öÔºö${allLines[2].join(' | ')}`);
            console.log(`‚ÜòÊñúÁ∑öÔºö${allLines[3].join(' | ')}`);
            console.log(`‚ÜóÊñúÁ∑öÔºö${allLines[4].join(' | ')}`);

            for (let i = 0; i < allLines.length; i++) {
                const [s1, s2, s3] = allLines[i];

                // Âà§Êñ∑ÊòØÂê¶Âêå‰∏ÄÁµÑÔºàÊîØÊè¥ WILDÔºâ
                const isSameOrWild = (a: string, b: string) =>
                    a === b || a === 'WILD' || b === 'WILD';

                const isWin = isSameOrWild(s1, s2) && isSameOrWild(s2, s3);

                if (isWin) {
                    winLines.push(lineNames[i]);

                    // ÊâæÂá∫‰∏≠ÁçéÁöÑ‰∏ª Symbol ÂêçÁ®±ÔºàÊéíÈô§ WILDÔºâ
                    const coreSymbol = [s1, s2, s3].find(s => s !== 'WILD') || 'WILD';
                    const payout = symbolPayTable[coreSymbol] ?? 0;
                    totalWin += payout;

                    console.log(`‚≠ê ${lineNames[i]} ‚Üí ${coreSymbol} √ó ${payout} ÂÄç`);

                    // Êí≠Êîæ‰∏≠ÁçéÂãïÁï´
                    const rows = i < 3 ? [rowMap[i], rowMap[i], rowMap[i]]
                              : i === 3 ? [0, 1, 2]  // ‚Üò
                              : [2, 1, 0];           // ‚Üó

                    const reels = [r1, r2, r3];
                    for (let j = 0; j < 3; j++) {
                        reels[j].playWinEffectAtRow(rows[j]);
                    }
                }
            }

            if (winLines.length > 0) {
                console.log(`üéâ ‰∏≠ÁçéÁ∑öÔºö${winLines.join('„ÄÅ')}`);
                console.log(`üí∞ Á∏ΩÂæóÂàÜÔºö${totalWin} ÈáëÂπ£`);
            } else {
                console.log('üò¢ Ê≤í‰∏≠Áçé');
            }

            this.isSpinning = false;
        }, 2800);
    }
}
